body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: whitesmoke;
  zoom: 0.9;
}

.clickable {
  cursor: pointer;
}

@media (max-width:550px) { 
  .App {
    padding-left: 20px;
    height: calc(100vh - 32px);
    overflow: auto;
  }

  .App-header {
    top: initial;
    bottom: 0;
    height: 50px;
    width: 100%;
  }
}


